import axios from 'axios'
import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import '../css/AdminLogin.css'
import Logo from '../images/logo.svg'

const AdminLoginScreen = () => {
    const history = useHistory()
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [errorMsg, setErrorMsg] = useState(null)

    const onClickLogin = (e) => {
        e.preventDefault()
        if (email === "") {
            setErrorMsg("Enter Username")
            return;
        }
        if (password === "") {
            setErrorMsg("Enter Password")
            return;
        }

        const data = {
            email: email,
            password: password
        }

        axios.post("https://gloed-previous.onrender.com/api/login", data)
            .then(response => {
                console.log(response.data)
                localStorage.setItem("accessToken", response.data.token)
                history.push("/admin")
            })
            .catch(err => {
                console.error(err.response)
                //setErrorMsg(err.data.message)
            })

    }

    return (
        <div className="body-div">
            <div className="form-div">
                <form action="" className=""
                    onSubmit={onClickLogin}>
                    <h2 className="form-text">Admin Login</h2>
                    {errorMsg ?
                        <div className="form-error">
                            <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="exclamation-triangle" class="svg-inline--fa fa-exclamation-triangle w-4 h-4 mr-2 fill-current" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                <path fill="currentColor" d="M569.517 440.013C587.975 472.007 564.806 512 527.94 512H48.054c-36.937 0-59.999-40.055-41.577-71.987L246.423 23.985c18.467-32.009 64.72-31.951 83.154 0l239.94 416.028zM288 354c-25.405 0-46 20.595-46 46s20.595 46 46 46 46-20.595 46-46-20.595-46-46-46zm-43.673-165.346l7.418 136c.347 6.364 5.609 11.346 11.982 11.346h48.546c6.373 0 11.635-4.982 11.982-11.346l7.418-136c.375-6.874-5.098-12.654-11.982-12.654h-63.383c-6.884 0-12.356 5.78-11.981 12.654z"></path>
                            </svg>
                            {errorMsg}
                        </div>
                        : null}

                    <input value={email} placeholder="Enter Username" type="text"
                        onChange={(e) => { setEmail(e.target.value) }} />
                    <input value={password} placeholder="Enter Password" type="password"
                        onChange={(e) => { setPassword(e.target.value) }} />
                    <button type="submit">Login</button>
                </form>
            </div>
        </div>
    )
}

export default AdminLoginScreen
